// Navbar.js
import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

//import ExpandingBoxComponent from '../ThreeJs/ExpandingBox'
import GlbLoader from '../ThreeJs/GLBLoader';

const CustomPageComponent = (props) => {
    const [pageId, setPageId] = useState(null);
    const [currentPage, setCurrentPage] = useState(null);

    useEffect(() => {
        if (props.id) {
            setPageId(props.id)
        }
    }, [props.id]);

    useEffect(() => {
        if (props.page) {
            setCurrentPage(props.page)
        }
    }, [props.page]);


    //////////////////////////////////////////////////////Return//////////////////////////////////////////////////////
    return (
        <PageContainer
        >
            <GlbLoader dt = {props.dt}>

            </GlbLoader>
            
        </PageContainer>
    );
};

export default CustomPageComponent;

//////////////////////////////////////////////////////Styling//////////////////////////////////////////////////////
const PageContainer = styled.div`

    position: fixed;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

    min-height: 100vh;
    height: auto;  
    width: 100vw;
    z-index: -1000;
    background-color: transparent;
`;

