import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

const ToggleButton = styled.button`
  background: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-left: auto;
  height: auto;
`;

const MorphingSvg = styled.svg`
  width: calc(15px + 2vmin);
  height: auto;
  transition: transform 0.3s ease;
  transform: ${({ $isOpen }) => ($isOpen ? 'rotate(90deg)' : 'rotate(0deg)')};
`;

const Path = styled.path`
  fill: none;
  stroke: white;
  stroke-width: 3;
  stroke-linecap: round;
  transition: d 0.3s ease;
`;

const DropDownToggle = (props) => {
    const [isOpen, setIsOpen] = useState(false);

    const handleClick = () => {
        props.show();
    };

    useEffect(()=>{
        if (props.showMobileMenu) {
            setIsOpen(true)
        } else {
            setIsOpen(false)
        }
    }, [props.showMobileMenu])

    return (
        <ToggleButton onClick={handleClick}>
            <MorphingSvg $isOpen={isOpen} viewBox="0 0 30 30" xmlns="http://www.w3.org/2000/svg">
                <Path
                    d={isOpen ? "M5 5 L25 25 M5 25 L25 5" : "M3 3 L27 3 M3 15 L27 15 M3 27 L27 27"}
                />
            </MorphingSvg>
        </ToggleButton>
    );
};

export default DropDownToggle;
