import React, { useState, useEffect } from 'react';
import './App.css';
import MainContainer from "./website_packages/containers/main_container";
import Navbar from "./website_packages/components/Navbar/Navbar";
import PAGE_DATA from "./data/PAGE_DATA"
import CONTACT_DATA from './data/CONTACT_DATA';

import PageComponent from './website_packages/components/Pages/PageComponent';
import CustomPageComponent from './website_packages/components/Pages/CustomPageComponent';
import Footer_1 from './website_packages/components/Footer/Footer_1';


function App() {
  const [currentPage, setCurrentPage] = useState(-2); // -2 is home
  const [currentHoveredPage, setCurrentHoveredPage] = useState(null);
  const [deviceType, setDeviceType] = useState('');

  useEffect(() => {
    const mediaQuery = window.matchMedia('(max-width: 768px)');

    const updateDeviceType = () => {
      if (mediaQuery.matches) {
        setDeviceType('mobile');
      } else {
        setDeviceType('desktop');
      }
    };

    updateDeviceType();

    const resizeListener = () => {
      updateDeviceType();
    };
    window.addEventListener('resize', resizeListener);

    return () => {
      window.removeEventListener('resize', resizeListener);
    };
  }, []);

  function setPage(val) {
    window.scrollTo({
      top: 0,
      behavior: 'smooth' // Optional: for smooth scrolling behavior
    });
    setCurrentPage(val);
  }

  useEffect(() => {
   //console.log("Current Page:", currentPage)
  }, [currentPage]);

  function setHovered(val) {
    setCurrentHoveredPage(val);
  }
  

  return (
    <div className='App'>
      <MainContainer>
        <Navbar pd={PAGE_DATA} sp={setPage} sh={setHovered} dt={deviceType} currentPage={currentPage}/>
        <PageComponent pd={PAGE_DATA} page={currentPage} hoveredPage={currentHoveredPage} sp={setPage} dt={deviceType}></PageComponent>
        <CustomPageComponent id={-2} page={currentPage} dt = {deviceType}></CustomPageComponent>
        <Footer_1 cd={CONTACT_DATA} dt={deviceType}></Footer_1>
      </MainContainer>

    </div>
  );
}

export default App;
