import React, { useState, useEffect } from 'react';
import styled, { keyframes } from 'styled-components';

//import SlideGallery from '../Gallery/SlideGallery';
import FilterComponent from '../Filters/FilterComponent';

const PageComponent = (props) => {

    const [pageData, setPageData] = useState(props.pd);
    const [pageTitles, setPageTitles] = useState(null);

    const [currentPage, setCurrentPage] = useState(-2); // -2 is home
    const [currentHoveredPage, setCurrentHoveredPage] = useState(-2); // -2 is home

    const [filteredKeys, setFilteredKeys] = useState(null);
    const [filteredElm, setFilteredElm] = useState(null);

    const [pageItems, setPageItems] = useState(null);
    const [blurBack, setBlurBack] = useState(false);

    //////////////////////////////////////////////////////Load Page Data//////////////////////////////////////////////////////
    useEffect(() => {
        setPageData(props.pd);
    }, [props.pd]);

    //Set the current page
    useEffect(() => {
        setCurrentPage(props.page);
    }, [props.page]);

    //Set hovered Page
    useEffect(() => {
        setCurrentHoveredPage(props.hoveredPage);
    }, [props.hoveredPage]);

    //Set the page headers (ie Main Pages)
    useEffect(() => {
        let temp = [];
        Object.keys(pageData).map((item, index) => (
            temp.push(item)
        ))
        setPageTitles(temp)
    }, [pageData]);

    //Used to generate keys from strings
    function arraysToString(arrays) {
        let ret_string = "|"

        //Each of the tag arrays
        for (let array in arrays) {
            ret_string += (arrays[array][0]).replace(/ /g, "~")

            //Each item in each array
            for (let i = 1; i < arrays[array].length; i++) {
                //console.log("Array",arrays[array])
                ret_string += "_" + arrays[array][i]
                //console.log("item", i, arrays[array].length,arrays[array][i])
            }
            ret_string += "|"
        }
        //console.log("ret_string",ret_string)
        return ret_string
    }

    //////////////////////////////////////////////////////Page Data To CSS//////////////////////////////////////////////////////
    useEffect(() => {
        if (pageData != null && pageTitles != null) {
            let temp = [];
            console.log("Page Data Loaded!")

            for (let i = 0; i < pageTitles.length; i++) { // Fix the loop condition
                let subkeys = Object.keys(pageData[pageTitles[i]]);
                let points = pageData[pageTitles[i]];

                let tag_keys = {}
                //All points on a page
                for (let item_title in points) {
                    if (item_title) {
                        let name = item_title.replace(/ /g, "~");
                        name = name + "_"


                        if (points[item_title]["Tags"]) {
                            let allTags = []
                            for (let t in points[item_title]["Tags"]) {

                                let temp_array = []
                                temp_array.push(t)
                                temp_array.push(points[item_title]["Tags"][t])
                                allTags.push(temp_array)
                            }

                            allTags = arraysToString(allTags)

                            tag_keys[item_title] = name + allTags
                        } else {
                            tag_keys[item_title] = name
                        }
                    }
                }
                temp.push(
                    <Page
                        key={pageTitles[i]}
                    >
                        {points && Object.keys(points).map((item, index) => (
                            item && item != "TOGGLE_DATA" && item != "DATE_COMP" && (
                                <DropdownItem
                                    $blur={blurBack && false ? true : false}
                                    key={tag_keys[item]}
                                >
                                    {item}
                                    {points && points[item] && Object.keys(points[item]).length > 0 && Object.keys(points[item]).map((sitem, index0) => (
                                        sitem != "Tags" &&
                                        <DropdownSubItem
                                            key={sitem + "_Item"}
                                        >
                                            <DropDownLogo href={points[item][sitem]['Link']}>
                                                <DropDownLogoTitle>
                                                    {sitem}
                                                </DropDownLogoTitle>
                                                {(points[item]["Tags"] != null && points[item]["Tags"]["Date"] != null) &&
                                                    Object.values(points[item]["Tags"]["Date"]).map((Date, index) => (
                                                        (
                                                            <DropDownDate
                                                                key={Date + "code"}
                                                            >
                                                                {Date}
                                                            </DropDownDate>)
                                                    ))
                                                }
                                                <DropDownLogoImg src={points[item][sitem]['Logo']} alt={points[item][sitem]['Logo']} />
                                            </DropDownLogo>
                                            <DropDownPoints
                                                key={sitem + "_Points"}
                                            >
                                                {(points[item][sitem]['Text'] != null) && (Object.values(points[item][sitem]['Text'])).map((text, index) => (
                                                    (
                                                        <DropDownSingleText
                                                            key={text + "_Text"}
                                                        >
                                                            {text}
                                                        </DropDownSingleText>)
                                                ))}
                                                {(points[item][sitem]['Points'] != null) && (Object.values(points[item][sitem]['Points'])).map((point, index2) => (
                                                    (Object.keys(points[item][sitem]['Points']).length > index2 && <DropDownSinglePoint
                                                        key={point}
                                                    >
                                                        {point}
                                                    </DropDownSinglePoint>)
                                                ))}
                                            </DropDownPoints>

                                            <TagContainer>
                                                {(points[item]["Tags"] != null && points[item]["Tags"]["Language"] != null) &&
                                                    Object.values(points[item]["Tags"]["Language"]).map((lang, index) => (
                                                        (
                                                            <TagItem
                                                                key={lang + "code"}
                                                            >
                                                                {index != Object.keys(points[item]["Tags"]["Language"]).length - 1 &&
                                                                    "#" + lang + ","}
                                                                {(points[item]["Tags"]["Program Description"] == null || points[item]["Tags"]["Program Description"].length == 0) && index == Object.keys(points[item]["Tags"]["Language"]).length - 1 &&
                                                                    "#" + lang}
                                                                {points[item]["Tags"]["Program Description"] != null && points[item]["Tags"]["Program Description"].length > 0 && index == Object.keys(points[item]["Tags"]["Language"]).length - 1 &&
                                                                    "#" + lang + ","}
                                                            </TagItem>
                                                        )
                                                    ))
                                                }
                                                {(points[item]["Tags"] != null && points[item]["Tags"]["Program Description"] != null) &&
                                                    Object.values(points[item]["Tags"]["Program Description"]).map((desc, index) => (
                                                        (
                                                            <TagItem
                                                                key={desc + "code"}
                                                            >
                                                                {index != Object.keys(points[item]["Tags"]["Program Description"]).length - 1 &&
                                                                    "#" + desc + ","}
                                                                {index == Object.keys(points[item]["Tags"]["Program Description"]).length - 1 &&
                                                                    "#" + desc}
                                                            </TagItem>
                                                        )
                                                    ))
                                                }
                                            </TagContainer>
                                        </DropdownSubItem>
                                    ))}
                                </DropdownItem>
                            )
                        ))}
                    </Page>
                );
            }
            setPageItems(temp);
        }
    }, [pageTitles]);

    //////////////////////////////////////////////////////Page Change Handlers//////////////////////////////////////////////////////

    //Reset the blur on each page
    useEffect(() => {
        setBlurBack(false)
    }, [currentPage]);

    const setBlur = (value) => {
        setBlurBack(value)
    }

    //Set the current page
    useEffect(() => {
        if ((currentPage >= 0 && pageItems[currentPage]) && filteredKeys && Object.keys(filteredKeys).length > 0) {
            const filteredItems = pageItems[currentPage].props.children.filter(item => filteredKeys.includes(item.key));
            setFilteredElm(filteredItems)
        } else {
            if (currentPage >= 0 && pageItems[currentPage]) {
                setFilteredElm(pageItems[currentPage])
            }
        }
    }, [filteredKeys, currentPage]);

    //////////////////////////////////////////////////////Return //////////////////////////////////////////////////////
    return (
        <PageContainer $blur={!currentPage >= 0}>
            <FilterComponent
                dt={props.dt}
                blur={setBlur}
                points={pageItems}
                currentPage={currentPage}
                currentHoveredPage={currentHoveredPage}
                setFilteredKeys={setFilteredKeys}
                sp={props.sp}
            />
            <BlurContainer $blur={blurBack} $vis={currentPage >= 0}>
                {filteredElm}
            </BlurContainer>
        </PageContainer>
    );
};

export default PageComponent;

//////////////////////////////////////////////////////Styling//////////////////////////////////////////////////////
//display: ${({ $vis }) => $vis ? 'flex' : 'none'};
const BlurContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    
    min-height: 90vh;
    height: auto;  
    width: 100vw;

    background-color: transparent;
    animation: ${({ $blur }) => $blur ? startblur : endblur} 1s ease forwards;
    
`;

const TagContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;


    height: auto;  
    width: 1auto;

    background-color: transparent;
    margin-bottom: 0.5vh;
`;

const TagItem = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;

    padding: 5px;
    height: auto;  
    width: 1auto;

    background-color: transparent;
`;

const PageContainer = styled.div`

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

    min-height: 90vh;
    height: auto;  
    width: 100vw;
    
    background-color: transparent;


    background-color: transparent;

`;

/*
    backdrop-filter: ${({ $blur }) => $blur ? 'blur(5px)' : 'none'};
    -webkit-backdrop-filter: ${({ $blur }) => $blur ? 'blur(5px)' : 'none'};
*/


const Page = styled.div`
    padding-top: 5vh;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    width: 100%;
    justify-content: space-evenly;
    align-items: center;
    height: 100%;

    background: transparent;
    
    
`;

const DropDownLogo = styled.a`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: left;
    align-items: center;

    margin-left: 1%;
    max-width: 99%;
    width: 99%;
    
    height: auto; 
    color: inherit;
    padding: 5px;

    text-align: left;
    font-weight: bold;
    text-decoration: none;

    background: transparent;
`;

const DropDownLogoImg = styled.img`
    display: flex;
    align-items: center;

    padding-left: 20px;
    max-height: 2vh; 
    width: auto;
    height: auto;
    object-fit: contain;

   
    text-align: center;
    font-weight: bold;
    background: transparent;
`;

const DropDownLogoTitle = styled.div`
    display: flex;
    
    flex-wrap: wrap;
    justify-content: left;
    align-items: center;
    align-content: flex-start; 
    
    width: auto;
    height: auto; 
    color: inherit;
    padding: 5px;

    text-align: left;
    font-weight: bold;
    text-decoration: none;
    background: transparent;
`;

const DropDownDate = styled.div`
    display: flex;
    color: rgb(18, 18, 18);
    align-items: right;
    margin-left: 2%;
    margin-top: 0%;
    width: auto;
    margin-right: auto;
    height: auto;
    text-align: right;
    font-weight: normal;
`;

//#FF0000;

const startblur = keyframes`
    from {
        opacity: 1;
        filter: blur(0px);
    }
    to {
        opacity: 0;
        filter: blur(3px);
    }
`;

const endblur = keyframes`
    from {
        opacity: 0;
        filter: blur(3px);
    }
    to {
        opacity: 1;
        filter: blur(0px);
    }
`;

//filter: ${({ blur }) => blur ? 'blur(10px)' : 'blur(0px) '};
//box-shadow: 0px 7px 29px 0px rgba(100, 100, 111, 1);

//background: rgb(18, 18, 18);
const DropdownItem = styled.div`
    display: flex;
    flex-direction: column;
    animation: ${endblur} 1s ease forwards;
    
    justify-content: center;
    align-items: left;
    
    max-width: 80%;

    width: auto;
    height: auto; 

    background: rgb(22,22,23,0.95);rgb(18,18,18);
    color: white;rgb(18, 18, 18);
    padding: 20px;
    border-radius: 20px;
    
    margin-top: 5vh;
   
    text-align: left;
    font-weight: 700;
    font-size: 1.2rem;
    overflow:hidden;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
`;

//#fa9f02
//#12fffb
//min-width: 40vw;
//min-height: 50vh;
const DropdownSubItem = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: auto;
    height:auto; 

    
    color: inherit;
    font-weight: 550;

    text-decoration: none;

    background: #282828;
    background: transparent;
    text-align: left;
`;

const DropDownPoints = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    width: auto;
    
    height: auto; 
    padding-top: 5px;
    margin-left: 2%;

    width: 98%;
    padding-bottom: 5px;
    
    font-weight: s;

    background: transparent;
    text-align: left;
    color: inherit;

`;

const DropDownSinglePoint = styled.li`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: auto;
    height:auto; 

    
    font-weight: 550;
    
    color: inherit;
    &::before {
    content: '\2022'; /* Unicode character for a bullet point */
    color: white;
    margin-right: 8px; /* Adjust the spacing between the bullet point and text */
    
    }

    
`;

const DropDownSingleText = styled.li`
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: left;
    width: auto;
    max-width: 776px;
    margin-bottom: 1vh;
    height:auto; 

    color: inherit;
    font-weight: 550;

    text-align: left;
`;



