import React, { useEffect, useState } from 'react';
import styled, { keyframes } from 'styled-components';
import LOGO from './../images/NJ_LOGO_2.png'

import DropDownToggle from './DropDownToggle';

const Toggle = (props) => {
  const [isChecked, setChecked] = useState(-2);
  const [isHovered, setHovered] = useState(-1);
  const [subTitles, setSubTitles] = useState(null);
  const [deviceType, setDeviceType] = useState('');
  const [showMobileMenu, setShowMobileMenu] = useState(false);

  //////////////////////////////////////////////////////Load Page Data//////////////////////////////////////////////////////
  useEffect(() => {
    setDeviceType(props.dt);
  }, [props.dt]);

  useEffect(() => {
    setChecked(props.currentPage)
  }, [props.currentPage]);

  //////////////////////////////////////////////////////Page Data to Css//////////////////////////////////////////////////////
  const renderToggleLabels = () => {
    const labels = [];
    labels.push(
      <ToggleTextLabel
        $items={props.items}
        key={'logo'}
        $isopen={isHovered === -2 ? true : undefined}
        $ischecked={isChecked === -2 ? true : undefined}
        onClick={handleClick(-2)}
        onMouseEnter={handleHover(-2)}
      >
        <Image src={LOGO}></Image>
      </ToggleTextLabel>
    );

    for (let i = 0; i < props.items; i++) {
      labels.push(
        <ToggleTextLabel
          $items={props.items}
          key={props.list[i]}
          $isopen={isHovered === i ? true : undefined}
          $ischecked={isChecked === i ? true : undefined}
          onClick={handleClick(i)}
          onMouseEnter={handleHover(i)}
        >
          {props.list[i]}
        </ToggleTextLabel>
      );
    }

    return labels;
  };

  //////////////////////////////////////////////////////Page Change Handlers//////////////////////////////////////////////////////
  //Handle User actions
  useEffect(() => {
    if (props.setPage != null && isChecked !== -1) {
      props.setPage(isChecked);
    }
  }, [isChecked]);

  useEffect(() => {
    props.setHovered(isHovered);
  }, [isHovered]);

  const handleClick = (index) => (e) => {
    e.stopPropagation();
    if (deviceType != 'desktop') {
      setShowMobileMenu(false)
    }
    setChecked(index);
  };

  const handleHover = (index) => (e) => {
    e.stopPropagation();
    setHovered(index);
  };
  function showMenu() {
    setShowMobileMenu(!showMobileMenu)
  };

  useEffect(() => {

    //console.log("ShowMobileMenu", showMobileMenu)

  }, [showMobileMenu]);

  //////////////////////////////////////////////////////Return//////////////////////////////////////////////////////
  //onMouseLeave={handleHover(null)}
  return (
    <ToggleContainer >
      {renderToggleLabels()[0]}

      {deviceType === 'desktop' ? (
        <ToggleLabel>
          {renderToggleLabels().slice(1)}
        </ToggleLabel>
      ) : (
        <DropDownToggle show={showMenu} showMobileMenu={showMobileMenu} />
      )}

      {deviceType === 'mobile' &&
        <FilterContainer translate={showMobileMenu ? "true" : "false"}>
          <ToggleLabelMobile>
            {renderToggleLabels().slice(1)}
          </ToggleLabelMobile>
        </FilterContainer>
      }
    </ToggleContainer>
  );

};

//showMobileMenu
export default Toggle;

//////////////////////////////////////////////////////Load Page Data//////////////////////////////////////////////////////

const DropDownSubContainer = styled.div`
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100vw;
  justify-content: center;
  align-items: center;
  
  height: auto;
  
  background: transparent;
`;

const DropdownItem = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  min-width: 33vw;
  max-width: 33vw;
  width: auto;
  height: auto; 
  color: white;
  padding: 5px;
  background: transparent;
  text-align: center;
  cursor: pointer;
  &:hover{
    scale: 1.1;
  }
`;

const ToggleContainer = styled.div`
  position: relative;
  z-index: 0;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  
  height: 100%;
  width: 100%;
  background: transparent;
`;

const ToggleLabel = styled.div`
  position: relative;
  z-index: 2;

  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;

  width: 100%;
  height: 100%;
  z-index:0;

  font-size: 1rem;
`;
const ToggleLabelMobile = styled.div`
  position: relative;
  z-index: 2;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;

  width: 100%;
  height: 70%;
  z-index:0;
  font-size: 2rem !important;
`;

const ToggleTextLabel = styled.button`
  margin: 0;
  padding: 0;
  border: none;
  background: none;
  color: inherit;
  font: inherit;
  cursor: pointer;
  outline: none;



  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  z-index: 4;
  cursor: pointer;
  overflow: hidden;
  position: relative;

  height: 100%;
  _width: ${({ $items }) => ($items ? (100 / $items) : 0)}vw;

  width: auto;

  opacity: ${({ $isopen }) => (!$isopen ? 0.7 : 1)};

  transition-duration: 0.01s;
  z-index:1;
  color:white;

  font-weight: thin;
`;

const Image = styled.img`
    
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;

    height: 100%;
    width: auto;
    
    background-color: #cfd1d1;
    border-radius: 5px;
    color: black;
`;

const translateAnimationDown = keyframes`
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0px);
  }
`;

const translateAnimationUp = keyframes`
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-100%);
  }
`;

const FilterContainer = styled.div`
    width: 100vw;
    height: 100vh;
    left: 0;
    position: fixed;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    z-index: -999;
    
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    background-color: rgb(22,22,23, 0.8);

    transform-origin: center bottom;
    animation: ${({ translate }) => translate === "true" ? translateAnimationDown : translateAnimationUp} 0.7s ease forwards;
`;