// Navbar.js
import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import Toggle from '../../controls/Toggle';

const NavBar = (props) => {

    const [pageData, setPageData] = useState(props.pd);
    const [pageTitles, setPageTitles] = useState(null);
    const [numPages, setNumPages] = useState(0);

    const [currentPage, setCurrentPage] = useState(-2); // -2 is home
    const [currentHoveredPage, setCurrentHoveredPage] = useState(null);
    const [deviceType, setDeviceType] = useState('');

    //////////////////////////////////////////////////////Load Page Data//////////////////////////////////////////////////////
    useEffect(() => {
        setDeviceType(props.dt);
    }, [props.dt]);

    useEffect(() => {
        setPageData(props.pd);
    }, [props.pd]);

    //When the page data is loaded, get the titles
    useEffect(() => {
        let temp = [];
        Object.keys(pageData).map((item, index) => (
            temp.push(item)
        ))
        setPageTitles(temp)
    }, [pageData]);

    //Set number of pages
    useEffect(() => {
        setNumPages(Object.keys(pageData).length)
    }, [pageTitles]);

    //////////////////////////////////////////////////////Page Change Handlers//////////////////////////////////////////////////////

    //Update which item is clicked
    function setPage(val) {
        setCurrentPage(val)
    }
    useEffect(() => {
        if (props.sp != null) {
            props.sp(currentPage)
        }
    }, [currentPage]);

    //Set Current Hovered Page
    function setHovered(val) {
        setCurrentHoveredPage(val)
    }
    useEffect(() => {
        if (props.sh != null) {
            props.sh(currentHoveredPage)
        }
    }, [currentHoveredPage]);

    //////////////////////////////////////////////////////Return//////////////////////////////////////////////////////
    return (
        <Bar
            $thick={deviceType === 'mobile' ? true : false}
        >
            <Toggle
                items={numPages}
                list={pageTitles}
                data={pageData}
                setPage={setPage}
                setHovered={setHovered}
                dt={deviceType}
                currentPage={props.currentPage}

            />
        </Bar>
    );
};

export default NavBar;

//////////////////////////////////////////////////////Styling//////////////////////////////////////////////////////
//height: calc(10px + 2vmin); $thick
//background-color: rgb(18,18,18);
const Bar = styled.div`
    position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    height: ${({ $thick }) => ($thick ? 'calc(10px + 4vmin)' : 'calc(10px + 2vmin)')};
    
    width: 100vw;
    z-index: 1000;
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(5px);
    background-color: rgb(22,22,23, 0.9);
    color: black;

    font-size: 1.3rem;
    
    padding: 5px;
    border-bottom: 1px solid black;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
`;

