import React, { useEffect, useRef, useState } from 'react';
import { ThreeJSApp } from './TJS_Utilities.js';
import AtomModel from './../../../data/AtomNuclear/AtomNuclear.glb'
import A1 from './../../../data/AtomNuclear/textures/electron_diffuse.jpg'
import A2 from './../../../data/AtomNuclear/textures/neutron_diffuse.jpg'
import A3 from './../../../data/AtomNuclear/textures/proton_diffuse.jpg'

const ExpandingBoxComponent = (props) => {
    const canvasRef = useRef();
    const [threeJsInstance, setThreeJsInstance] = useState(null);
    const [state, setState] = useState({
        currentPage: null,
        scrollDistance: 0,
    });

    const { currentPage, scrollDistance } = state;

    useEffect(() => {
        window.addEventListener('scroll', trackScrollDistance);
        return () => {
            window.removeEventListener('scroll', trackScrollDistance);
        };
    }, []);

    useEffect(() => {
        if (props.page !== currentPage) {
            setState((prevState) => ({ ...prevState, currentPage: props.page }));
        }
    }, [props.page, currentPage]);

    useEffect(() => {
        if (!threeJsInstance) {
            const app = new ThreeJSApp();
            setThreeJsInstance(app);
        }
    }, [threeJsInstance]);

    /*
    useEffect(() => {
      if (threeJsInstance && !canvasRef.current.children.length) {
        const canvas = threeJsInstance.createCanvas();
        canvasRef.current.appendChild(canvas);
        const shape = threeJsInstance.createShape(
          canvas,
          'torusKnot',
          {
            radius: 14,
            tube: 1,
            tubularSegments: 300,
            radialSegments: 20,
            p: 3,
            q: -7,
          },
          'blue',
          { x: 0, y: 0, z: -40 },
          { x: 0, y: 0, z: 0 }
        );
        shapeRef.current = shape;
      }
    }, [threeJsInstance]);
    */

    useEffect(() => {
        if (threeJsInstance && !canvasRef.current.children.length) {
            const canvas = threeJsInstance.createCanvas();
            canvasRef.current.appendChild(canvas);
            
            threeJsInstance.createGlbModel(
                AtomModel,
                [A1, A2, A3],
                'blue',
                { x: 0, y: 0, z: 3 },
                { x: 0, y: 0, z: 0 }

            ).then((shape) => {
                shapeRef.current = shape;
                console.log(shape)

            }).catch((error) => {
                console.error('Error loading GLB model:', error);
            });
        }
    }, [threeJsInstance]);

    useEffect(() => {

        if (shapeRef.current && false) {
            const animateShape = () => {
                let aniAmount = 0.001 * scrollDistance;
                //console.log(shapeRef)
                //shapeRef.current.geometry.parameters.radius += aniAmount * 10;
                //shapeRef.current.rotation.x = aniAmount;
                //shapeRef.current.rotation.y = aniAmount;
                //shapeRef.current.rotation.z = aniAmount;
                if(aniAmount > 3){
                    aniAmount = 3;
                }
                //let rot = threeJsInstance.createVector(0,0,1+aniAmount)
                //threeJsInstance.moveSceneTo(rot)
                //this.scene.rotation = aniAmount;
                //threeJsInstance.renderer.render(threeJsInstance.scene, threeJsInstance.camera);
            };

            //animateShape();

            return () => {
                threeJsInstance.renderer.dispose();
            };
        }
    }, [scrollDistance, threeJsInstance]);

    const trackScrollDistance = () => {
        setState((prevState) => ({
            ...prevState,
            scrollDistance: window.pageYOffset,
        }));
    };

    const shapeRef = useRef(null);

    return (
        <div
            ref={canvasRef}
            style={{ font: "100px", backgroundColor: "transparent", width: "100vw", height: "100vh" }}
        ></div>
    );
};

export default ExpandingBoxComponent;
