// Navbar.js
import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

import phoneIcon from './../../images/icons/phone_iphone_FILL0_wght400_GRAD0_opsz48.svg'
import emailIcon from './../../images/icons/mail_FILL0_wght400_GRAD0_opsz48.svg'
import githubIcon from './../../images/icons/github2.png'
import linkedinIcon from './../../images/icons/linkedin.png'

const Footer_1 = (props) => {
    const [phoneData, setPhoneData] = useState({});
    const [gitHubData, setGithubData] = useState({});
    const [emailData, setEmailData] = useState([]);
    const [linkedinData, setLinkedinData] = useState([]);

    const [contactData, setContactData] = useState({});
    useEffect(() => {
        if (props.cd) {
            //console.log('Contact Data', props.cd)
            setContactData(props.cd)
            setPhoneData(props.cd["Phone"])
            setGithubData(props.cd["Github"])
            setEmailData(props.cd["Emails"])
            setLinkedinData(props.cd["Linkedin"])
        }
    }, [props.cd])

    //Homepage
    return (
        <Footer>
            {(contactData != null &&
                <>
                    {Object.keys(contactData).map((item, index) => (
                        <ContactItem key={item + "_Contact"}>
                            <a
                                href={item == 'Email' ? 'mailto:' + contactData[item]['Links'][0] : item=='Phone' ? 'tel:'+ contactData[item]['Links'][0] : contactData[item]['Links'][0]}
                                >
                                <IconImage
                                    src={contactData[item]["Icons"][0]}

                                />
                            </a>
                            <ContactItemSub
                                key={item + "Contact_Item_Sub"}
                            >
                                {item != 'Phone' && contactData[item]['Profiles'] != null &&
                                    Object.values(contactData[item]['Profiles']).map((sitem, index) => (
                                        <ContactItemSubSub
                                            key={item + "_Contact_Item_Sub_Sub_1" + index}
                                        >
                                            {item != 'Email' &&
                                                <Item key={sitem + "REG_ITEM"} href={contactData[item]['Links'][index]}>
                                                    {props.dt != 'mobile' && sitem}
                                                </Item>
                                            }
                                            {item == 'Email' &&
                                                <Item key={sitem + "EMAIL_ITEM"} href={'mailto:' + contactData[item]['Links'][index]}>
                                                    {props.dt != 'mobile' && sitem}
                                                </Item>
                                            }
                                        </ContactItemSubSub>
                                    ))
                                }
                                {item == 'Phone' && contactData[item] != null &&
                                    Object.keys(contactData[item]).map((sitem, index) => (
                                        <ContactItemSubSub key={item + "_Contact_Item_Sub_Sub_2" + index}>
                                            {sitem != 'Icons' && sitem != 'Links' &&
                                                <CountryDiv
                                                    key={sitem + "_Country_Contact"}
                                                >
                                                    {props.dt != 'mobile' && sitem}
                                                    {Object.values(contactData[item][sitem]["Cell"]).map((ssitem, id1) => (
                                                        <Item key={ssitem + "_Country_Contact_Number"} href={'tel:'+contactData[item]['Links'][index]}>
                                                            {props.dt != 'mobile' && ssitem}
                                                        </Item>
                                                    ))}
                                                </CountryDiv>
                                            }
                                        </ContactItemSubSub>
                                    ))
                                }
                            </ContactItemSub>

                        </ContactItem>
                    ))}
                </>
            )}
        </Footer >
    );
};

export default Footer_1;

const Footer = styled.div`
    margin-top: 4vh;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
    height: auto;
    width: 100vw;
    background-color: rgb(18, 18, 18);
    color: white;
    font-size: 1rem;
    
    box-shadow: rgba(0, 0, 0, 0.4) 0px -2px 4px, rgba(0, 0, 0, 0.3) 0px -7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
`;

const ContactItem = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: auto;
    width: auto;
    background-color: transparent;
    text-decoration: none;
    color: white;
    padding: 2px;
    padding-top: 2.5vh;
    padding-bottom: 2.5vh;
`;

const ContactItemSub = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: space-evenly;
    height: auto;
    width: auto;
    background-color: transparent;
`;

const ContactItemSubSub = styled.div`
    height: auto;
    width: auto;
    background-color: transparent;
`;

const Item = styled.a`
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: space-evenly;
    height: auto;
    width: auto;
    background-color: transparent;
    text-decoration: none;
    color: white;
`;

const IconImage = styled.img`
    display: flex;
    align-items: right;

    max-width: 2.2rem;
    width: auto;
    max-height: 47px;
    height: auto;
    object-fit: contain;
    text-align: center;
    font-weight: lighter;
    background: transparent;
    margin-right: 1vw;
`;

const CountryDiv = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: space-evenly;
    height: auto;
    width: auto;
    background-color: transparent;
`;



const Link = styled.a`
    width: auto;
    height: auto;
`;

//Phone Data
const PhoneData = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: auto;
    width: auto;
    background-color: transparent;
`;



//Github Data
const GithubData = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: auto;
    min-width: 33%;
    width: auto;
    background-color: transparent;
    text-decoration: none;
    color: white;
`;

const NumberDiv = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: auto;
    width: auto;
    background-color: transparent;
`;
const NumberDivSub = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: auto;
    width: auto;
    background-color: transparent;
`;


//Email Data
const EmailData = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: auto;
    min-width: 33%;
    width: auto;
    background-color: transparent;
`;




