import React, { useEffect, useState } from 'react';
import styled, { keyframes } from 'styled-components';
import DownArrow from './../../images/icons/arrow_forward_ios_FILL0_wght400_GRAD0_opsz49.svg'

const FilterComponent = (props) => {

    const [points, setPoints] = useState(null);
    const [tsData, setTSData] = useState({});

    const [selectors, setSelectors] = useState(null);
    const [tags, setTags] = useState(null);

    const [selectedTags, setSelectedTags] = useState({});

    const [showMenu, setShowMenu] = useState(false);
    const [currentPage, setCurrentPage] = useState(null);
    const [filteredKeys, setFilteredKeys] = useState([]);

    //////////////////////////////////////////////////////Load Page Data//////////////////////////////////////////////////////

    useEffect(() => {
        if (props.points) {
            setPoints(props.points)
        }
    }, [props.points])

    useEffect(() => {
        if (points) {
            let temp_ts = {};
            let idx = 0; // Define idx here

            points.forEach(parent => {
                if (parent && parent.props.children) {
                    let tempTags = {};
                    let tempTagMasterKeys = {};
                    let allSelectors = [];

                    parent.props.children.forEach(child => {
                        let temp_data = child.key;
                        if (child.key) {
                            temp_data = child.key.split("|");

                            for (let i = 1; i < temp_data.length - 1; i++) {
                                let temp_array = temp_data[i].split("_");
                                let temp_selector = temp_array[0];
                                if (!allSelectors.includes(temp_selector) && temp_selector) {
                                    allSelectors.push(temp_selector);
                                }

                                for (let j = 1; j < temp_array.length; j++) {
                                    let temp_tags = temp_array[j].split(/,/g)
                                    if (!tempTags[temp_selector]) {
                                        tempTags[temp_selector] = new Set();
                                    }

                                    temp_tags.forEach(tag => {
                                        if (tag != '') {
                                            tempTags[temp_selector].add(tag);
                                            if (!tempTagMasterKeys[tag]) {
                                                tempTagMasterKeys[tag] = new Set();
                                                tempTagMasterKeys[tag].add(child.key)
                                            } else {
                                                tempTagMasterKeys[tag].add(child.key)
                                            }
                                        }
                                    })
                                }
                            }
                        }
                    });

                    let uniqueTags = {};
                    Object.keys(tempTags).forEach(key => {
                        uniqueTags[key] = Array.from(tempTags[key]).sort().reverse();
                    });

                    let uniqueKeys = {};
                    Object.keys(tempTagMasterKeys).forEach(key => {
                        uniqueKeys[key] = Array.from(tempTagMasterKeys[key]).sort();
                    });

                    if (uniqueTags.length > 0 || allSelectors.length > 0) {
                        temp_ts[idx] = {
                            "tags": uniqueTags,
                            "selectors": allSelectors,
                            "items": tempTagMasterKeys,
                        }
                    }
                    idx++;
                }
            });
            setTSData(temp_ts)
        }
    }, [points]);

    useEffect(() => {
       //console.log("hover:", props.currentHoveredPage, "current:", props.currentPage)
        if (props.currentHoveredPage >= 0) {
           //console.log("1")
            setCurrentPage(props.currentHoveredPage)
        } else if (props.currentPage >= 0) {
           //console.log("2")
            initSelTags()
            setCurrentPage(props.currentPage)
        } else {
           //console.log("3")
            setCurrentPage(-1)
        }
    }, [props.currentPage, props.currentHoveredPage])

    useEffect(() => {
        if (tsData && tsData[currentPage] && Object.keys(tsData[currentPage]).length > 0) {
            setShowMenu(true)
        } else {
            initSelTags()
            setShowMenu(false)
        }
    }, [currentPage])

    useEffect(() => {
        //console.log("setShowMenu", showMenu)
        //initSelTags()
    }, [showMenu]);

    //////////////////////////////////////////////////////Tag Arrays Creation//////////////////////////////////////////////////////

    useEffect(() => {
        if (Object.keys(tsData).length > 0) {
            let tempAllTagArrays = {}
            Object.keys(tsData).forEach(page => {
                tempAllTagArrays[page] = tsData[page]["tags"];
            })
            setTags(tempAllTagArrays)
        } else {
            setShowMenu(false)
        }
    }, [tsData])

    useEffect(() => {
        if (Object.keys(tsData).length > 0) {
            let tempAllSelectorArrays = {}
            Object.keys(tsData).forEach(page => {
                tempAllSelectorArrays[page] = tsData[page]["selectors"];
            });
            setSelectors(tempAllSelectorArrays)
        }
    }, [tags])

    useEffect(() => {
        if (Object.keys(tsData).length > 0) {
            let tempSelectedTags = {}
            Object.keys(tsData).forEach(page => {
                tempSelectedTags[page] = new Set();
            });
            setSelectedTags(tempSelectedTags)
        }
    }, [selectors]);

    //////////////////////////////////////////////////////Selected Tags and filtering//////////////////////////////////////////////////////
    useEffect(() => {
        if (tsData && tsData[currentPage] && Object.keys(tsData[currentPage]).length > 0) {
            let found_keys = []
            selectedTags[currentPage].forEach(tag => {
                tsData[currentPage]["items"][tag].forEach(item => {
                    found_keys.push(item);
                });
            });
            setFilteredKeys(found_keys)
        } else {
            setFilteredKeys([])
        }
    }, [selectedTags])

    useEffect(() => {
        props.setFilteredKeys(filteredKeys)

        if(filteredKeys){
            setShowMenu(false);
        }
    }, [filteredKeys]);

    //////////////////////////////////////////////////////User Click Handling//////////////////////////////////////////////////////
    const hideFilter = () => {
        if (!showMenu && tsData && currentPage >= 0 && tsData[currentPage]) {
            setShowMenu(true);
        } else {
            setShowMenu(false);
        }
    }

    /*
    if (Object.keys(selectedTags).length === 0) {
        hideFilter();
    }
    */

    function initSelTags () {
        let tempSelectedTags = {}
            Object.keys(tsData).forEach(page => {
                tempSelectedTags[page] = new Set();
            });
            setSelectedTags(tempSelectedTags)
    }

    function setSelTags (cur_page, tag_list) {
        let tempSelectedTags = {}
            Object.keys(tsData).forEach(page => {
                tempSelectedTags[page] = new Set();
            });
            tempSelectedTags[cur_page] = tag_list;
            setSelectedTags(tempSelectedTags)
    }

    const handleTagClick = (tag) => {
        //Current page is not hovered
        if (props.currentPage != null && props.currentPage == currentPage) {
            if (selectedTags != null && currentPage > 0) {
                const currentPageTags = selectedTags[currentPage];

                if (!currentPageTags.has(tag)) {
                    currentPageTags.add(tag);
                } else {
                    currentPageTags.delete(tag);
                }
                
                setSelectedTags({
                    ...selectedTags,
                    [currentPage]: currentPageTags
                });
            }

        //Current Page is hovered
        } else if (props.currentPage != null && props.currentPage != currentPage) {
            //console.log("Here s:", selectedTags, "C:", currentPage, "Check:", selectedTags != null && currentPage > 0)
            if (selectedTags != null && currentPage > 0) {
                const currentPageTags = selectedTags[currentPage];

                if (!currentPageTags.has(tag)) {
                    currentPageTags.add(tag);
                } else {
                    currentPageTags.delete(tag);
                }

                setSelTags(currentPage, currentPageTags);

                if(props.sp){
                    props.sp(currentPage)
                }
            }
            
        } else {
            //console.log("rip:",props.currentPage ,"s:", selectedTags, "C:", currentPage, "Check:", selectedTags != null && currentPage > 0)
        }
    };

    //////////////////////////////////////////////////////Javascript//////////////////////////////////////////////////////
    return (
        <FilterContainer translate={showMenu ? "true" : "false"} $thick = {props.dt === 'mobile'}>
            <FilterContainerSub >
                {currentPage > 0 && Object.values(selectors[currentPage]).map((selector, idx) => (
                    (selector != "Date1" &&
                        <Selector key={selector + idx}>
                            <TagList>
                                {tags != null && Object.values(tags[currentPage][selector]).map((tag, index) => (
                                    <TagItem
                                        key={tag + "_" + index}
                                        $isselected={selectedTags[currentPage].has(tag) ? true : false}
                                        $op={showMenu}
                                        onClick={() => handleTagClick(tag)}
                                    >
                                        {tag}
                                    </TagItem>
                                ))}
                            </TagList>
                        </Selector>
                    )
                ))}
            </FilterContainerSub>

            <ShowToggle onClick={() => hideFilter()} $op = {tsData && tsData[currentPage] && Object.keys(tsData[currentPage]).length > 0} >
                <FilterShowImage src={DownArrow} translate={showMenu ? "true" : "false"} />
            </ShowToggle>
        </FilterContainer>
    );
};
export default FilterComponent;

//////////////////////////////////////////////////////Styling//////////////////////////////////////////////////////

const translateAnimationDown = keyframes`
  from {
    transform: translateY(-90%);
  }
  to {
    transform: translateY(0px);
  }
`;

const translateAnimationUp = keyframes`
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-90%);
  }
`;

const rotateAnimationDown = keyframes`
  from {
    transform: rotate(90deg);
  }
  to {
    transform: rotate(-90deg);
  }
`;

const rotateAnimationUp = keyframes`
  from {
    transform: rotate(-90deg);
  }
  to {
    transform: rotate(90deg);
  }
`;

// animation: ${translateAnimationUp} 1s ease forwards;
//${({ translate }) => translate === "true" ? "0.5s" : "0.5s"}
const FilterContainer = styled.div`
    width: 100%;
    height: auto;
    left: 0;
    position: fixed;
    top: ${({ $thick }) => ($thick ? 'calc(20px + 4vmin)' : 'calc(20px + 2vmin)')};
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 999;
    background: transparent;
    transform-origin: center bottom;
    animation: ${({ translate }) => translate === "true" ? translateAnimationDown : translateAnimationUp} 0.7s ease forwards;
`;

const FilterContainerSub = styled.div`
    width: 100%;
    height: auto;
    min-height:334px; /* Adjust this height property to control the height of FilterContainer */
    max-height: 80vh; /* Set a maximum height to prevent it from taking up the entire viewport */
    overflow-y: auto; /* Add scroll if content overflows */
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(5px);
    background-color: rgb(22,22,23, 0.8);
`;

const ShowToggle = styled.button`
    cursor: pointer;
    margin-right: 5px;

    border: none;
    background: transparent;

    opacity: ${({ $op }) => $op ? '1' : '0'};
    
    min-height: 10%;
    max-height: 10%;
    height: auto;
    width: auto;

    display: flex;
    flex-direction: row;
    margin-left: auto;

    color: white;
    font-weight: normal;
    
    &:hover {
        background-color: RGB(0,0,0,0);
        opacity: 1;
    }
`;

const FilterShowImage = styled.img`
    display: flex;
    min-height: 10%;
    max-height: 10%;

    width: auto;
    height: auto;

    animation: ${({ translate }) => translate === "true" ? rotateAnimationDown : rotateAnimationUp} 0.5s ease forwards;
`;

//opacity: ${({ $op }) => $op ? 1 : 0};
const Selector = styled.div`
    width: auto;
    height: auto;

    
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: start;
    align-items: center;
    margin: 10px;

    background: transparent;
`;

//tagOpacity
const TagList = styled.ul`
    list-style: none;
    width: 100%;
    height: auto;
    
    display: flex;
    flex-direction: column;
    flew-wrap: wrap;

    padding: 5px;
    margin: 0;
    background: transparent;
    
`;

const TagTitle = styled.div`
    list-style: none;
    width: auto;
    height: auto;
    
    display: flex;
    flex-direction: row;
    flew-wrap: wrap;
    color: black;
    font-weight: normal;
    padding: 2px;
    background: transparent;
`;


//box-shadow: 2px 1px 0px 0px white;
//background-color: ${({ $isselected }) => $isselected ? 'RGB(0,0,0,1)' : 'RGB(255,255,255,0.5)'};

const TagItem = styled.button`
    cursor: pointer;
    margin-right: 5px;
    padding: 5px;
    margin: 5px;
    border-radius: 5px;
    
    border: none;
    background: transparent;

    box-shadow: 2px 1px 0px 0px ${({ $isselected }) => $isselected ? 'red' : 'white'};

    opacity: ${({ $isselected }) => $isselected ? '1' : '0.7'};
    height: auto;
    width: auto;
    min-width: 5vw;
    font-size: 1rem;
    color: white;
    font-weight: normal;

    
    &:hover {
        background-color: RGB(0,0,0,0.7);
        opacity: 1;
    }
`;

const SelectedTagItem = styled(TagItem)`
    background-color: #007bff;
    color: #fff;

    &:hover {
        background-color: #0056b3;
    }
`;

const ClearButton = styled.button`
    margin-top: 10px;
`;

